<template>

  <div class="signup-container">
    <div class="signup-title-container">
      <h1 class="signup-title">회원가입</h1>
    </div>

    <div class="signup-input-container">

      <div class="signup-id-container">
        <Input v-model="user.userId"
               @change="errorFields.userId = validateUserId($event.target.value)"
               placeholder="아이디"
               :readonly="checkedId"
               class="signup-input-id" />
        <Button @click="onDoubleCheckId"
                name="중복확인"
                class="signup-id-check-btn"/>
      </div>
      <InputError :errorMsg="errorFields.userId" class="signup-id-error"/>

      <Input v-model="user.userPw"
             type="password"
             @change="errorFields.userPw = validateUserPw($event.target.value, user.userId)"
             placeholder="비밀번호"
             class="signup-input-pw"/>
      <InputError :errorMsg="errorFields.userPw" class="signup-pw-error"/>

      <Input v-model="user.confirmPw"
             type="password"
             placeholder="비밀번호 확인"
             class="signup-input-confirm-pw"/>
      <InputError :errorMsg="errorFields.confirmPw" class="signup-confirm-error"/>

      <Input v-model="user.userName"
             @change="errorFields.userName = validateUserName($event.target.value)"
             placeholder="이름"
             class="signup-input-pw"/>
      <InputError :errorMsg="errorFields.userName" class="signup-name-error"/>

    </div>

    <div class="signup-btn-container">
      <Button @click="onSignUp" name="회원가입" class="signup-btn-submit"/>
    </div>
  </div>

</template>

<script setup>
import Button from "@/components/Button.vue";
import Input from "@/components/Input.vue";
import InputError from "@/components/InputError.vue";
import {defineComponent, ref} from "vue";
import {doubleCheckId, signUp} from "@/api/userService";
import {validateConfirm, validateUserId, validateUserName, validateUserPw} from "@/util/userValidUtil";
import {useRouter} from "vue-router";
import {useStore} from "vuex";

defineComponent({
  name: 'SignUp',
  components: {
    Button, Input, InputError
  }
});
const router = useRouter();
const store = useStore();

/* 사용자 정보 */
const user = ref({
  userId: '', /* 사용자 아이디 */
  userPw: '', /* 사용자 비밀번호 */
  confirmPw: '', /* 확인용 비밀번호 */
  userName: '', /* 사용자 이름 */
});

/* 사용자정보 유효성검증 */
const errorFields = ref({
  userId: '',
  userPw: '',
  confirmPw: '',
  userName: '',
});

/* 중복확인 체크여부 */
const checkedId = ref(false);

/**
 * 아이디를 중복확인 합니다.
 *
 * @returns {Promise<boolean>}
 */
const onDoubleCheckId = async () => {
  try {
    errorFields.value.userId = validateUserId(user.value.userId);
    if (errorFields.value.userId.length > 0) {
      return false;
    } else {
      errorFields.value.userId = '';
    }

    const idCheckResult = await doubleCheckId(user.value.userId);

    if (idCheckResult) {
      checkedId.value = true;
      alert("사용가능한 아이디 입니다.")
    } else {
      checkedId.value = false;
      alert('해당 아이디는 중복입니다.');
    }
  } catch ({message}) {
    console.error(message);
  }
}

const onSignUp = async () => {
  if (validateForm()) {
    return false;
  } else if (!checkedId.value) {
    alert('중복확인이 필요합니다.');
    return false;
  }

  try {
    const signUpId = await signUp(user.value);
    store.commit('setUserId', signUpId);

    router.push('/login');

  } catch ({data}) {
    for (const field in data) {
      errorFields.value[field] = data[field];
    }
  }



};

/**
 * 폼 데이터를 검증합니다.
 * @returns {boolean}
 */
const validateForm = () => {
  errorFields.value.userId = validateUserId(user.value.userId);
  errorFields.value.userPw = validateUserPw(user.value.userPw, user.value.userId);
  errorFields.value.confirmPw = validateConfirm(user.value.userPw, user.value.confirmPw);
  errorFields.value.userName = validateUserName(user.value.userName);

  for (const field in errorFields.value) {
    if (field.length > 0) {
      return false;
    }
  }

  return true;
}

</script>


<style scoped>

  .signup-container {
    width: 300px;
    margin: 0 auto;
  }

  .signup-title-container {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
  }

  .signup-title {
    font-size: var(--title-font-size);
  }

  .signup-input-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10px;
  }

  .signup-id-container {
    width: 300px;
    display: flex;
    justify-content: space-between;
  }

  .signup-input-id {
    flex-basis: 70%;
  }

  .signup-id-check-btn {
    flex-basis: 27%;
  }

  .signup-id-error, .signup-pw-error, .signup-confirm-error, .signup-name-error {
    margin-right: auto;
    padding: 5px 3px;
  }

  .signup-btn-container {
    display: flex;
    gap: 10px;
    flex-direction: column;
    align-items: center;
  }

  .signup-btn-container button {
    box-sizing: border-box;
    width: 300px;
    height: 40px;
    background-color: var(--sub-color-violet);
  }

</style>